.about-us-all-section {
    position: relative;
    background-image: url("../../assests/Page2/BackgroundFooter.jpg");
    
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    height: 420px;
    width: 100%;
    z-index: 1;
}

.about-us-all-section::after {
    content: "";
    background-image: url(data:image/png;base64,);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    z-index: -1;
}

.HeaderForAll{
    color: #1F2278;
}

.ShapeForAll{
    background-color: #565ACF;
}